/* ---------- Theme ---------- */
:root{
  --ink:#111827;         /* gray-900 */
  --muted:#6b7280;       /* gray-500 */
  --bg-soft:#ffffff;
  --orange-500:#f97316;
  --orange-600:#ea580c;
  --red-500:#ef4444;
  --red-600:#dc2626;
  --green-600:#16a34a;
  --blue-600:#2563eb;
  --amber-400:#f59e0b;
  --pink-500:#ec4899;

  --radius-lg:16px;
  --radius-xl:20px;
  --shadow-1:0 8px 24px rgba(0,0,0,.10);
  --shadow-2:0 14px 34px rgba(0,0,0,.16);
}

/* ---------- Layout helpers ---------- */
.container-7xl{max-width:1120px;margin:0 auto;padding:0 16px;}
.center{text-align:center;}
.grid{display:grid;gap:2rem;}
@media (min-width:768px){
  .grid-4{grid-template-columns:repeat(4,1fr);}
  .grid-3{grid-template-columns:repeat(3,1fr);}
  .grid-2{grid-template-columns:repeat(2,1fr);}
}

/* ---------- Hero ---------- */
.hero{position:relative;overflow:hidden;}
.hero .inner{position:relative;z-index:1;}
.btn-primary{
  display:inline-flex;align-items:center;justify-content:center;
  padding:16px 28px;border-radius:999px;font-weight:800;
  background:#fff;color:#dc4a0a;text-decoration:none;
  box-shadow:var(--shadow-1);transition:transform .2s ease, box-shadow .2s ease, background .2s ease;
}
.btn-primary:hover{transform:translateY(-1px);box-shadow:var(--shadow-2);background:#fff4e6;}

.hero-pills{display:flex;align-items:center;gap:12px;}
.pill{
  display:inline-flex;align-items:center;gap:8px;
  padding:10px 14px;border-radius:999px;
  background:rgba(255,255,255,.2);backdrop-filter:saturate(160%) blur(2px);
  font-weight:500;
}

/* ---------- Stats ---------- */
.stats-section{background:#fff;padding:64px 0;}
.stats-grid{
  display:grid;gap:2rem;text-align:center;
  grid-template-columns:repeat(2,1fr);
}
@media (min-width:768px){.stats-grid{grid-template-columns:repeat(4,1fr);} }

.stat-title{font-size:40px;font-weight:900;line-height:1;margin-bottom:.35rem;}
.stat-note{color:var(--muted);}
.stat--o .stat-title{color:var(--orange-600);}
.stat--r .stat-title{color:var(--red-600);}
.stat--g .stat-title{color:var(--green-600);}
.stat--b .stat-title{color:var(--blue-600);}

/* ---------- Why Us ---------- */
.why-section{
  background:linear-gradient(90deg,#fff7ed 0%, #ffe7e7 100%);
  padding:80px 0;
}
.section-head{margin-bottom:3.5rem;text-align:center;}
.section-head h2{font-size:clamp(28px,4vw,40px);font-weight:900;margin:0 0 .5rem;color:var(--ink);}
.section-head p{color:var(--muted);font-size:18px;margin:0;}

.why-card{
  background:#fff;border-radius:var(--radius-xl);box-shadow:var(--shadow-1);
  padding:32px;text-align:center;transition:box-shadow .25s ease, transform .25s ease;
}
.why-card:hover{box-shadow:var(--shadow-2);transform:translateY(-2px);}

.badge{
  width:80px;height:80px;border-radius:999px;margin:0 auto 1.25rem;
  display:flex;align-items:center;justify-content:center;color:#fff;
}
.badge--orange{background:linear-gradient(90deg,#fb923c,#ef4444);}
.badge--red{background:linear-gradient(90deg,#ef4444,#f472b6);}
.badge--amber{background:linear-gradient(90deg,#fbbf24,#fb923c);}
.why-card h3{font-size:24px;font-weight:800;margin:12px 0;color:var(--ink);}
.why-card p{color:#6b7280;font-size:18px;margin:0;}

/* ---------- Bestsellers ---------- */
.bestsellers{background:#fff;padding:80px 0;}
.btn-gradient{
  display:inline-flex;align-items:center;justify-content:center;gap:8px;
  padding:14px 24px;border-radius:999px;font-weight:700;text-decoration:none;
  color:#fff;background:linear-gradient(90deg,var(--orange-500),var(--red-500));
  box-shadow:var(--shadow-1);transition:filter .2s ease, transform .2s ease, box-shadow .2s ease;
}
.btn-gradient:hover{filter:brightness(1.06);transform:translateY(-1px);box-shadow:var(--shadow-2);}

/* ---------- Testimonials ---------- */
.testimonials{
  background:linear-gradient(90deg,#fff8cc 0%, #ffedd5 100%);
  padding:80px 0;
}
.testimonial-card{
  background:#fff;border-radius:var(--radius-xl);box-shadow:var(--shadow-1);
  padding:32px;
}
.testimonial-card .quote{
  color:#374151;font-size:18px;line-height:1.7;font-style:italic;margin:0 0 1.25rem;
}

