/* src/pages/home.css */

/* ---------- Theme ---------- */
:root{
  --grad1:#f97316; /* orange-500 */
  --grad2:#ef4444; /* red-500    */
  --grad3:#ec4899; /* pink-500   */
  --ink:#111827;   /* gray-900   */
  --muted:#6b7280; /* gray-500   */

  --orange-600:#ea580c;
  --red-600:#dc2626;
  --green-600:#16a34a;
  --blue-600:#2563eb;

  --radius-lg:16px;
  --radius-xl:20px;
  --shadow-1:0 8px 24px rgba(0,0,0,.12);
  --shadow-2:0 14px 32px rgba(0,0,0,.18);
}

/* ---------- Layout helpers ---------- */
.container-7xl{max-width:1120px;margin:0 auto;padding:0 16px;}
.center{text-align:center;}
.grid-4{display:grid;grid-template-columns:1fr;gap:32px;}
@media(min-width:768px){.grid-4{grid-template-columns:repeat(2,1fr);}}
@media(min-width:1024px){.grid-4{grid-template-columns:repeat(4,1fr);}}

/* ---------- Page bg (matches gradient) ---------- */
.min-h-screen{min-height:100vh;}
.bg-page{
  background:linear-gradient(180deg,#fff7ed 0%,#fff1c2 45%,#ffe1e9 100%);
}

/* ---------- Hero ---------- */
.hero{
  position:relative;color:#fff;overflow:hidden;
  background:linear-gradient(90deg,var(--grad1),var(--grad2),var(--grad3));
}
.hero .overlay{position:absolute;inset:0;background:rgba(0,0,0,.2);}
.hero .inner{position:relative;padding:7rem 0;}
.hero h1{
  font-size:clamp(40px,6vw,72px);font-weight:900;letter-spacing:-.02em;margin:0 0 12px;
}
.hero h2{font-size:clamp(24px,4vw,44px);font-weight:800;margin:0 0 8px;}
.hero .lead{font-size:clamp(18px,2.5vw,22px);color:#ffe8c7;margin:0 0 16px;}
.hero .sub{font-size:clamp(16px,2.2vw,20px);color:#fff;opacity:.9;max-width:720px;margin:0 auto 28px;}
.hero .cta{display:flex;gap:16px;align-items:center;justify-content:center;flex-wrap:wrap;}
.hero .btn-primary{
  background:#fff;color:var(--orange-600);font-weight:800;border-radius:999px;
  padding:16px 32px;box-shadow:var(--shadow-2);text-decoration:none;display:inline-flex;
}
.hero .btn-primary:hover{background:#fff6db;}
.pill{
  display:inline-flex;align-items:center;gap:8px;background:rgba(255,255,255,.2);
  padding:8px 14px;border-radius:999px;font-weight:600;
}

/* ---------- Stats ---------- */
.stats{background:#fff;padding:64px 0;}
.stats .grid{display:grid;gap:28px;text-align:center;}
@media(min-width:768px){.stats .grid{grid-template-columns:repeat(4,1fr);}}
.stat .big{font-size:40px;font-weight:900;margin-bottom:6px;}
.stat .muted{color:var(--muted);}
.stat.o .big{color:var(--orange-600);}
.stat.r .big{color:var(--red-600);}
.stat.g .big{color:var(--green-600);}
.stat.b .big{color:var(--blue-600);}

/* ---------- Why Us ---------- */
.why{
  background:linear-gradient(90deg,#fff7ed,#ffe7e7);
  padding:80px 0;
}
.section-title{margin-bottom:48px;text-align:center;}
.section-title h2{font-size:clamp(28px,4vw,40px);font-weight:900;margin:0 0 8px;}
.section-title p{color:var(--muted);font-size:18px;}
.card{
  background:#fff;border-radius:var(--radius-xl);box-shadow:var(--shadow-1);padding:32px;
}
.card:hover{box-shadow:var(--shadow-2);}
.badge{
  width:80px;height:80px;border-radius:999px;margin:0 auto 20px;
  display:flex;align-items:center;justify-content:center;color:#fff;
  font-weight:800;
}
.badge.orange{background:linear-gradient(90deg,#fb923c,#ef4444);}
.badge.red{background:linear-gradient(90deg,#ef4444,#f472b6);}
.badge.amber{background:linear-gradient(90deg,#fbbf24,#fb923c);}
.card h3{font-size:24px;font-weight:800;margin:14px 0;}
.card p{color:#6b7280;font-size:18px;}

/* ---------- Bestsellers ---------- */
.bestsellers{background:#fff;padding:80px 0;}
.btn-gradient{
  color:#fff;background:linear-gradient(90deg,var(--grad1),var(--grad2));
  padding:14px 24px;border-radius:999px;font-weight:700;text-decoration:none;
  display:inline-flex;gap:8px;box-shadow:var(--shadow-1);
}
.btn-gradient:hover{filter:brightness(1.05);box-shadow:var(--shadow-2);}

/* ---------- Testimonials ---------- */
.testimonials{
  background:linear-gradient(90deg,#fff8cc,#ffedd5);
  padding:80px 0;
}
.testimonials .quote{font-style:italic;color:#374151;font-size:18px;line-height:1.7;}
